/* main container */
.main {
	position: relative;
	width: 100%;
}

/* mainpage background */
.home-background {
	padding-top: 60px;
	position: relative;
	width: 100%;
	height: 960px;
}
/* home page Youtube API 자동재생 */
.youtube {
	position: relative;
	height: 100%;
	background-color: #333;
	overflow: hidden;
}
.youtube .youtube__area {
	width: 100%;
	background-color: #000;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
.youtube .youtube__area::before {
	content: '';
	display: block;
	width: 100%;
	height: 0;
	padding-top: 56.25%;
}
.youtube .youtube__cover {
	background-color: rgba(0, 0, 0, 0.1);
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
#player {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
.youtube .inner {
	height: 100%;
} /* home page Youtube API 끝*/

/* home page typing Effect */
.type_effect_container {
	position: absolute;
	z-index: 1;
	top: 458px;
	left: 50%;
	transform: translate(-50%, -50%);
	font-family: 'MaruBuri';
	width: 550px;
	color: #ffffff;
	font-weight: 700;
	font-size: 50px;
	line-height: 180%;
	font-size: 50px;
	text-align: center;
	letter-spacing: -0.015em;

	text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.type_effect_text {
	display: inline;
}

.type_effect_container span {
	color: #ff2068;
}

.type_effect_cursor {
	display: inline;
	-webkit-animation: cursor 0.5s infinite;
	animation: cursor 0.5s infinite;
}
@-webkit-keyframes cursor {
	50% {
		opacity: 0;
	}
}
@keyframes cursor {
	50% {
		opacity: 0;
	}
} /* home page typing Effect 끝*/

