/* nav */
.nav.content-container {
	width: 100%;
	height: 60px;
	position: fixed;
	top: 0;
	left: 0;
	display: flex;
	align-items: center;
	z-index: 100;
	padding: 0 10px;
	-webkit-box-shadow: 0px 5px 5px -5px rgba(0, 0, 0, 0.45);
	box-shadow: 0px 5px 5px -5px rgba(0, 0, 0, 0.45);
	background-color: #fff;
}

.nav__logo-wrapper {
	display: flex;
	gap: 10px;
	margin-left: 220px;

	cursor: pointer;
}
.nav__logo-text {
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	font-size: 18px;
	line-height: 160%;

	letter-spacing: -0.015em;

	display: flex;
	align-items: center;
	justify-content: center;
	color: #000000;
}
.nav__menu-wrapper {
	display: flex;
	flex-grow: 1;
}

.nav__menu-wrapper ul {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	font-size: 18px;
	margin-right: 240px;
}

.nav__dropdown-list {
	text-decoration: none;
	cursor: pointer;
	list-style: none;
	text-align: center;
	width: 200px;
	padding: 20px 0;
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 700;
	letter-spacing: -0.015em;
	overflow: hidden;
	transition: 0.3s;
}
.nav__dropdown-list:hover {
	background-color: #f9f9f9;
	color: #ff2068;
}
.nav__dropdown-content {
	display: none;
	position: absolute;
	transform: translate(0, 20px);
	min-width: 200px;
	background-color: #f9f9f9;
}
.nav__dropdown-subtitle {
	color: #000;
	padding: 15px 0;
	text-align: center;
	font-family: 'MaruBuri';
	font-style: normal;
	font-weight: 600;
	font-size: 16px;
	letter-spacing: -0.015em;
}
.nav__dropdown-subtitle:hover {
	background-color: #ececec;
}
.nav__dropdown-list:hover .nav__dropdown-content {
	display: block;
}
