/* kakaomap */
#kakaomap {
	width: 1438px;
	height: 361px;
	margin: auto;
	position: relative;
}
.overlay-block {
	position: relative;
}
.overlay-container {
	border-radius: 30px;
	box-shadow: 0 3px 10px 0 rgb(0 0 0 / 17%);
	white-space: nowrap;

	display: inline-block;
	cursor: pointer;
}
.overlay-wrapper {
	display: flex;
	align-items: center;
	position: relative;
	z-index: 10;
	padding: 11px 9px 11px 9px;
	border: 1px solid #0475f4;
	border-radius: 30px;
	background: #fff;
}
.icon-area {
	position: relative;
	width: 24px;
	height: 24px;
	border: 1px solid transparent;
	border-radius: 50%;
	background: #0475f4;
	box-sizing: border-box;
}
.icon-place {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	fill: #fff;
	width: 24px;
	height: 24px;
	display: inline-block;
}

.text-area {
	position: relative;
	margin: 0 5px 0 10px;
}
.maintext-place {
	color: #000;
	line-height: 1.6rem;
}
.maintext {
	display: block;
	white-space: nowrap;
	font-size: 1.1rem;
	font-weight: 600;
	letter-spacing: -0.2px;
	color: #000;
	line-height: 1.6rem;
}

.overlay-pointer {
	width: 0;
	height: 0;
	border-width: 8px 5px 0;
	border-style: solid;
	border-color: #0475f4 transparent transparent;
	display: inline-block;
	vertical-align: top;
	position: absolute;
	left: 25px;
	bottom: -7px;
	margin-top: -2px;
	z-index: 11;
}
.overlay-pointer::after {
	width: 0;
	height: 0;
	border-width: 6px 4px 0;
	border-style: solid;
	border-color: #fff transparent transparent;
	position: absolute;
	top: -8px;
	left: 50%;
	z-index: 10;
	transform: translateX(-50%);
	content: '';
}
.overlay-pointer::before {
	opacity: 0.1;
	position: absolute;
	bottom: -2px;
	left: -5px;
	width: 10px;
	height: 3px;
	background: #000;
	filter: blur(1px);
	content: '';
}
.show-kakaomap {
	position: absolute;
	top: 20px;
	right: 20px;
	padding: 9px 8px 7px 10px;
	border: solid 1px rgba(0, 0, 0, 0.1);
	border-radius: 2px;
	background: rgba(255, 255, 255, 0.95);
	box-sizing: border-box;
	font-size: 1rem;
	color: rgba(36, 36, 36, 1);
	line-height: 1.5rem;
	text-decoration: none;
	cursor: pointer;
	z-index: 11;
}
.show-kakaomap-marker {
	fill: rgba(51, 51, 51, 1);
	width: 15px;
	height: 15px;
	margin: 4px 0 0 7px;
	display: inline-block;
	vertical-align: top;
	overflow: hidden;
	cursor: pointer;
	color: rgba(36, 36, 36, 1);
	z-index: 11;
}

